/* istanbul ignore next */
define('app/libs/angular.dcb-img-fallback',['require','angular'],function(require) {
    'use strict';
    /**
 * Angular Image utils
 * (c) 2014 Daniel Cohen. http://dcb.co.il
 * License: MIT
 * https://github.com/dcohenb/angular-img-fallback
 */
 var
        angular = require("angular");

angular.module('libs.img-fallback', [])
    .directive('fallbackSrc', function() {
        var missingDefault = "data:image/jpeg;base64,";
        return {
            restrict: 'A',
            link: function(scope, element, attr) {
                element.on('error', function() {
                    element[0].src = attr.fallbackSrc ? attr.fallbackSrc : missingDefault;
                });
            }
        };
    })
    .directive('loadingSrc', function() {
        var loadingDefault = "data:image/jpeg;base64,";
        return {
            restrict: 'A',
            link: function(scope, element, attr) {
                element[0].src = attr.loadingSrc ? attr.loadingSrc : loadingDefault;
                var img = angular.element('<img />');
                img.src = scope.ngSrc;
                img.on('load', function() {
                    element[0].src = img.src;
                });
            }
        };
    });

});


